@import "./variable";
@import "./mixin";
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,300&display=swap");
html {
    font-size: 62.5%;
    @media screen and (max-width: 768px) {
        // scroll-behavior: smooth;
    }
}
body {
    font-family: "Roboto", sans-serif;
    font-size: 1.8rem;
    line-height: 1.5;
    text-rendering: optimizeSpeed;
    -webkit-font-smoothing: antialiased;
    width: 100vw;
    overflow-x: hidden !important;
    z-index: 0;
    background-color: $mainClr;
    padding-bottom: 200px;
}

* {
    padding: 0;
    margin: 0;
    text-decoration: none;
    color: unset;
    border: none;
    outline: none;
    background-color: unset;
    -webkit-tap-highlight-color: transparent;
    &:hover {
    }
}
button {
    cursor: pointer;
}
p {
    font-size: 1.4rem;
    color: $text;
    line-height: 1.7;
}
/* width */
::-webkit-scrollbar {
    width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f100;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: rgba(204, 204, 204, 0.416);
    border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}
.admin {
    width: 100%;
    position: relative;
    cursor: grab;
    &::after {
        content: " ";
        width: 100%;
        height: 100%;
        border: 1px solid $admin;
        position: absolute;
        text-align: right;
        top: 0;
        border-radius: 5px;
        right: 0;
        color: $white;
        font-size: 1.2rem;
        opacity: 0;
        transition: 0.25s ease;
        font-weight: normal;
    }
    &::before {
        content: "ðŸ–Š Chá»‰nh sá»­a";
        position: absolute;
        background-color: rgba(204, 204, 204, 0.17);
        top: 6px;
        right: 5px;
        padding: 2px 5px;
        border-radius: 3px;
        color: $white;
        font-size: 1.2rem;
        opacity: 0;
        z-index: 9;
        transition: 0.25s ease;
        font-weight: normal;
    }
    &:hover {
        &::after {
            opacity: 1;
        }
        &::before {
            opacity: 1;
        }
    }
}
.adminIMG {
    width: 100%;
    height: 100%;
    position: relative;
    &::after {
        content: "ðŸ–Š Chá»‰nh sá»­a";
        text-align: right;
        position: absolute;
        background-color: rgba(94, 94, 94, 0.25);
        width: 100%;
        height: 100%;
        @include flex(center);
        color: $white;
        cursor: pointer;
        top: 0;
        left: 0;
        z-index: 9;
        transition: 0.35s ease;
        opacity: 0;
        pointer-events: none;
    }
    &:hover {
        &::after {
            opacity: 1;
        }
    }
}
.opa0 {
    width: 0;
    opacity: 0;
    position: absolute;
    pointer-events: none;
}
img {
    background: linear-gradient(
        rgba(204, 204, 204, 0.141),
        rgba(143, 143, 143, 0.128)
    );
}
p,
h1 {
    word-wrap: break-word;
    width: 100%;
}
